html {
  font-size: 10px;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  background-color: black;
  color: gray;
}

p {
  font-size: 1.6rem;
}

small {
  font-size: 1.2rem;
}

.container {
  margin: 0 auto;
  max-width:1000px;
  padding: 40px;
}

.title {
  font-size: 4.4rem;
  text-align: center
}

.form {
  display:grid;
}

.label{
  color: white;
  font-size: 2rem;
  font-weight: bolder;
  margin-bottom: 0.2rem;
  text-transform: uppercase;
  margin-right: 0.5rem;
}

.input{
  
  font-size: 1.6rem;
  padding: 0.5rem 2rem;
  line-height: 2.8rem;
  border-radius: 20px;
  border: 1.3px solid black;
  margin-bottom: 1rem;
}

.button{
  background-color: black;
  color:gray;
  padding: 1rem;
  border-radius: 20px;
  font-size: 1.6rem;
  cursor: pointer;
  transition: background-color 250ms;
}

.button-hover{
  background-color: red;
}

@media (min-width:786px){
  .form{
    grid-template-columns: auto 1fr auto;
    grid-gap: 1rem;
    align-items: center;
  }

  .input{
    margin-bottom: 0;
  }

}

.card {
  padding: 2rem 4rem;
  border-radius: 10px;
  border: solid white 10px;

  box-shadow: 2px 1px 5px white;
  margin-top: 2rem;
  background-color: black;
  color: white;
  display: flex;
}

.card--title {
  margin-bottom: 0.5rem;
  font-size: 3.2rem;
}

.card--image {
  margin: 20px 20px;
}